import { FC } from "react";
import css from './footer.module.css'
import logo from '../../assets/Logo.svg'
import twitter from '../../assets/twitter.svg'
import instagram from '../../assets/instagram.svg'
import facebook from '../../assets/facebook.svg'
import { SubscribtionForm } from "../../components/subscribeform/subscribeform";

type props = {

}

export const Footer: FC<props> = ({ }) => {
    return <footer className={css.footerContainer}>
        <section className={css.footerWidgets}>
            <div className={css.first}>
                <img src={logo} alt='logo' />
                <p>
                         2019      
                </p>
            </div>
            <div>
                <label className={css.heading}></label>
                <label> </label>
                <label></label>
                <label> </label>
                <label></label>
            </div>
            <div>
                <label className={css.heading}> </label>
                <label> </label>
                <label> </label>
                <label></label>
                <label></label>
            </div>
            <div>
                <label className={css.heading}> </label>
                <label></label>
                <label></label>
                <label>  </label>
            </div> 
            <div className={css.fifth}>
                <label className={css.heading}></label>
                <p>
                         
                </p>
                <SubscribtionForm
                    type={'email'} 
                    placeholder="Email" />
            </div>
        </section>
        <section className={css.copyrightBar}>
            <label>Copyright  2018 Toxin .   .</label>
            <div className={css.icons}>
                <img src={twitter} alt='twitter' />
                <img src={facebook} alt='facebook' />
                <img src={instagram} alt='instagram' />
            </div>
        </section>
    </footer>
}