import { Form, Formik } from "formik";
import { FC } from "react";
import { useNavigate } from "react-router";
import { Button } from "../../components/button/button";
import { FieldInput } from "../../components/fieldInput/fieldInput";
import css from './enter.module.css'

type props = {
    title: string
}

export const Login: FC<props> = ({ title }) => {

    const initialValues = {
        email: '',
        password: ''
    }

    const navigate = useNavigate()

    return <section className={css.enterContainer}>
        <Formik
            initialValues={initialValues}
            onSubmit={(values, { setSubmitting }) => {
                alert(JSON.stringify(values))
                setSubmitting(false)
            }}>
            {({ setSubmitting }) => <>
                <Form>
                    <h1>{title}</h1>
                    <div className={css.doubleInputs}>
                        <FieldInput fieldType="default"
                            name="email"
                            type='email'
                            placeholder="Email" />
                        <FieldInput fieldType="default"
                            name="password"
                            type='password'
                            placeholder="" />
                    </div>
                    <Button text=""
                        isSubmit
                        onClick={() => console.log('clicked')}
                        type="big" />
                    <div className={css.isAccauntExist}>
                        <p>    Toxin</p>
                        <Button onClick={() => navigate('/register')}
                            type="empty" isSubmit={false}
                            text="" />
                    </div>
                </Form>
            </>}

        </Formik>
    </section>
}