import { Form, Formik } from "formik";
import { FC } from "react";
import { useNavigate } from "react-router";
import { Button } from "../../components/button/button";
import { FieldInput } from "../../components/fieldInput/fieldInput";
import { FieldRadioButton } from "../../components/fieldRadioInput/fieldradiobutton";
import { FieldToggleButton } from "../../components/fieldtoggle/fieldtogglebutton";
import css from './register.module.css'

type props = {
    title: string
}

type registerData = {
    name: string
    surname: string
    sex: 'male' | 'female'
    burthdate: string
    email: string
    password: string
    isSpecs: boolean
}

export const Register: FC<props> = ({ title }) => {

    const initialValues: registerData = {
        name: '',
        surname: '',
        sex: 'male',
        burthdate: '',
        email: '',
        password: '',
        isSpecs: false,
    }

    const navigate = useNavigate()

    return <section className={css.registerContainer}>
        <Formik
            initialValues={initialValues}
            onSubmit={(values, { setSubmitting }) => {
                alert(JSON.stringify(values));
                setSubmitting(false);
            }}>
            {({ setSubmitting }) => <>
                <Form>
                    <h1>{title}</h1>
                    <div className={css.doubleInputs}>
                        <FieldInput name="name" type='text' 
                            placeholder="" fieldType="default" />
                        <FieldInput name="surname" type='text' 
                            placeholder="" fieldType="default" />
                    </div>
                    <FieldRadioButton label="" name="sex" value="male" />
                    <FieldRadioButton label="" name="sex" value="female"/>
                    <FieldInput name="burthdate" type='date' 
                        fieldType='masked' title=" " />
                    <div className={css.doubleInputs}>
                        <FieldInput name="email" type='email' placeholder="Email" 
                            fieldType="default" title="    " />
                        <FieldInput name="password" type='password' 
                            placeholder="" fieldType="default" />
                    </div>
                    <FieldToggleButton label=" " name="isSpecs" />
                    <Button type="big" 
                        text="" 
                        isSubmit 
                        onClick={() => console.log('clicked')} />
                    <div className={css.isAccauntExist}>
                        <p>    Toxin</p>
                        <Button onClick={() => navigate('/login')}
                            type="empty" isSubmit={false}
                            text="" />
                    </div>
                </Form>
            </>}

        </Formik>
    </section>
}